import React from 'react';
import { NavLink } from 'react-router-dom';
import PropTypes from 'prop-types';

const List = ({ data }) => {
  return (
    <ul>
      {data.map(e => (
        <li key={e.id}>
          <NavLink to={`/movies/${e.id}`}>{e.title || e.name}</NavLink>
        </li>
      ))}
    </ul>
  );
};

List.propTypes = {
  data: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      title: PropTypes.string,
      name: PropTypes.string,
    }),
  ).isRequired,
};

export default List;
